import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { List } from './components/List';

function App() {
  return (
    <Router basename="/kojaks-react-app">
      <div className="App">
        <header className="App-header">
          <nav>
            <ul>
              <li>
                <Link className="ra-link" to="/">Home</Link>
              </li>
              <li>
                <Link className="ra-link" to="/about">About</Link>
              </li>
              <li>
                <Link className="ra-link" to="/users">Users</Link>
              </li>
              <li>
                <Link className="ra-link" to="/list">List</Link>
              </li>
            </ul>
          </nav>
          <Switch>
            <Route path="/about">
              This is content for React "about" page...
            </Route>
            <Route path="/users">
              This is content for React "users" page...
            </Route>
            <Route path="/list" component={List} />
            <Route path="/">
              This is content for React "home" page...
            </Route>
          </Switch>
          <img src={logo} className="App-logo" alt="logo" />
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
          </a>
        </header>
      </div>
    </Router>
  );
}

export default App;
